import React from "react";

const List = ({ people, Delete }) => {
  return (
    <>
      {people.map((person) => {
        return (
          <article key={person.id} className="person">
            <img src={person.image} alt="img"></img>
            <div>
              <h4>{person.name}</h4>
              <p>{person.age} years</p>
            </div>
            <button onClick={() => Delete(person.id)}>delete</button>
          </article>
        );
      })}
    </>
  );
};
export default List;
